.button, input[type=submit] {
  display: flex;
  background: transparent;
  padding: 0.6em 1em;
  cursor: pointer;
  text-decoration: none !important;
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  transition: 0.2s ease-in-out; }
  .button.arrow, input[type=submit].arrow {
    padding: 0.5em 0.3em; }
    .button.arrow.right img, input[type=submit].arrow.right img {
      margin: -4px 0 0 4px; }
    .button.arrow img, input[type=submit].arrow img {
      margin: -4px 4px 0 0; }
  .button svg, input[type=submit] svg {
    position: relative;
    z-index: 1;
    top: 3px;
    left: 13px;
    margin-right: 13px;
    transition: 0.2s ease-in-out; }
  @media only screen and (max-width: 1024px) {
    .button, input[type=submit] {
      font-size: 94%; } }
  @media only screen and (max-width: 600px) {
    .button, input[type=submit] {
      width: 100%;
      display: block;
      font-size: 110%; } }

